.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
}

.tasks {
  max-width: 600px;
  margin: auto;
  display: flex;
  justify-content: space-around;
  padding: 0px 20px;
}

.tasks__panel {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.tasks__control {
  display: flex;
  justify-content: space-between;
}

.tasks__control button:first-child {
  margin-right: 20px;
}

.tasks__list {
  border: 1px solid lightgrey;
  margin: 0px 0px 40px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background: #cbe2ff;
  padding: 8px;
  width: 100%;
  position: relative;
  box-sizing: border-box;
}

.tasks__container {
  margin: 0 0 8px 0;
  background: white;
  border: #bdd7f7 1px solid;
}

.tasks__item {
  width: 100%;
  cursor: move;
}

.task__content {
  padding: 10px 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  transition: 0.5s;
}

.task__content>* {
  /* pointer-events: none; */
}

.task__content.done {
  background-color: #91c1ff;
}

.task__content.done .task__inner {
  text-decoration: line-through;
}

input[type='checkbox'],
button {
  cursor: pointer;
}

input[type='checkbox'] {
  width: 50px;
  height: 50px;
}

.task__buttons {
  display: flex;
  flex-direction: column;
}

.task__inner {
  text-align: left;
}

.task__edit {
  width: 100%;
}

.diagram {
  background: lightblue;
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}

.diagram__field {
  border: 1px solid lightgrey;
  height: 50px;
  background: #cbe2ff;
  display: flex;
  align-items: flex-end;
  width: 100%;
}

.diagram__bar {
  background-color: #91c1ff;
  height: 100%;
  /*width: 100%;*/
  transition: 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.diagram_caption {
  margin-left: 50px;
}

.onDragStart {
  visibility: hidden;
  transition: 0.5s;
}

.onDragOver {
  opacity: 0.5;
  transition: 0.5s;
}

button {
  font-family: 'Roboto', sans-serif;
  background: #3992ff;
  border: none;
  padding: 5px 20px;
  margin-bottom: 10px;
  width: 100%;
  height: 40px;
  color: white;
  text-transform: uppercase;
}

input[type="text"] {
  width: 100%;
  margin-bottom: 10px;
  border: 1px solid lightgrey;
  height: 40px;
  padding-left: 5px;
  box-sizing: border-box;
  font-size: 16px;
}